import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import View from '../view';

const Card = styled(({ borderStyle, borderColor, backgroundColor, shape, boxShadow, borderRadius, type, ...props }) => (
	<View {...props} />
))`
	border-width: 1px;
	border-style: ${props => props.borderStyle};
	border-radius: ${props => props.borderRadius};
	border-color: ${props => props.borderColor};
	background-color: ${props => props.backgroundColor};
	box-shadow: ${props => props.boxShadow};
	display: ${props => props.type};
`;
export default Card;

Card.propTypes = {
	borderStyle: PropTypes.string
	// borderColor: PropTypes.oneOf(Object.keys(theme.color)),
	// backgroundColor: PropTypes.oneOf(Object.keys(theme.color))
};

Card.defaultProps = {
	borderStyle: 'none',
	backgroundColor: 'white',
	boxShadow: '0 2px 8px 0 rgba(0, 0, 0, 0.08)',
	borderRadius: '2px',
	borderColor: 'black',
	type: 'inline-block'
};
